@import "../../colors/Colors.css";

ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}

li {
  background-color: var(--primary);
  color: var(--secondary);
  font-weight: bold;
  font-size: 1.1rem;
  margin: 10px;
  padding: 2px 10px;
  border-radius: 5px;
  cursor: pointer;
}
